<template src="./template.html"></template>
<style src="./style.css"></style>

<script>
export default {
  data() {
    return {
      nb_illust: 100,
      p_follow: 1,
      price: 1,
      nb_items: 5,
      round_coef: 100,
    }
  },
  computed: {
    p() {
      return (1/this.nb_illust*this.nb_items+this.p_follow/100)/this.nb_items;
    },
    table_items() {
      let items = [];
      for(let t=1; t<=this.nb_items; t++) {
        let sum = 0;
        for(let k=1; k<=t; k++) {
          sum += 1/(this.nb_items-k+1);
        }
        items.push({
          "欲しい絵を何枚得るか": t + " 種類",
          "ガチャ回数の期待値": Math.round(sum/this.p*this.round_coef)/this.round_coef + " 回",
          "課金額の期待値": Math.round(sum*this.price/this.p*this.round_coef)/this.round_coef + " 円"
        });
      }
      return items;
    }
  }
}
</script>
